<template>
  <section class="main">
    <div v-if="isDeviceLinked && hasUuid">
      <!-- Mode for linked devices with UUID parameter -->
      <div class="loader-container">
        <loader size="50px" />
        <p class="processing-text">{{ $t('LINKING_YOUR_COMPUTER') }}</p>
      </div>
    </div>
    <div v-else-if="isDeviceLinked && !hasUuid">
      <!-- Mode for linked devices WITHOUT UUID parameter -->
      <div class="message-container">
        <h1 class="title">{{ $t('NOTICE_SCAN_QR_CODE_THAT_IS_SHOWN_ON_COMPUTER') }}</h1>
      </div>
    </div>
    <div v-else>
      <!-- Mode for unlinked devices showing QR code -->
      <h1 class="title">{{ $t('SCAN_QR_TO_LINK_COMPUTER') }}</h1>
      <div class="subtitle-wrapper">
        <html-with-links
          tag="p"
          class="subtitle"
          :html="$t('LINK_APPLE_SILICON_MAC_DESC', { href: '/appdb-tools' })"
        />
        <html-with-links
          tag="p"
          class="subtitle tv-link"
          :html="$t('CLICK_TO_LINK_TV', {href: '/link/tv'})"
        />
      </div>
      
      <!-- Show loader while fetching new QR code -->
      <div v-if="isLoading" class="loader-container">
        <loader size="50px" />
        <p class="processing-text">{{ $t('LOADING') }}</p>
      </div>
      
      <!-- Show QR code once loaded -->
      <div v-else class="qr-container">
        <qrcode class="qr-code" :value="qrCodeUrl" :background="qrCodeBg" />
        <div class="timer" v-if="expiresTime">
          {{ timeRemaining }}
        </div>
      </div>
    </div>
    
    <!-- Back link shown in all modes -->
    <div class="back-link-container">
      <a href="javascript:history.back()" class="g-button back-link">{{ $t('GO_BACK') }}</a>
    </div>
  </section>
</template>

<script>
import { mapState } from 'vuex';
import QrcodeVue from 'qrcode.vue';
import Loader from '@/components/Loader';
import HtmlWithLinks from '@/components/HtmlWithLinks.vue';

export default {
  components: { qrcode: QrcodeVue, Loader, HtmlWithLinks },
  data() {
    return {
      uuid: '',
      expiresTime: null,
      linkToken: null,
      isLoading: true,
      pollingInterval: null,
      checkSessionInterval: null,
      lastUpdated: Date.now(),
    };
  },
  computed: {
    ...mapState(['theme']),
    qrCodeBg() {
      // Always return white for QR code background regardless of theme
      return '#FFFFFF';
    },
    isDeviceLinked() {
      return this.$store.getters.isDeviceLinked;
    },
    hasUuid() {
      return !!this.$route.query.uuid;
    },
    domain() {
      return window.location.host;
    },
    qrCodeUrl() {
      if (!this.uuid) return '';
      return `https://${this.domain}/link-qr/?uuid=${this.uuid}`;
    },
    timeRemaining() {
      if (!this.expiresTime) return '';
      
      const timestamp = this.lastUpdated;
      const now = Math.floor(Date.now() / 1000);
      const remaining = Math.max(0, this.expiresTime - now);
      
      return `${remaining} ${this.$t('SECONDS_REMAINING')}`;
    }
  },
  methods: {
    async getLinkSession() {
      try {
        // Clean up any existing intervals/timeouts first
        this.cleanupIntervals();
        
        // Set loading state
        this.isLoading = true;
        
        // Clear previous state
        this.uuid = '';
        this.expiresTime = null;
        this.linkToken = null;
        
        // Get new session
        const response = await this.$api.call('get_link_session', {}, false);
        
        if (response.success && response.data) {
          this.uuid = response.data.uuid || '';
          this.expiresTime = response.data.expires_at || null;
          this.linkToken = response.data.link_token || null;
          
          // Start checking for session updates
          this.startCheckingSession();
          
          // End loading state
          this.isLoading = false;
        } else {
          console.error('Failed to get link session:', response);
          setTimeout(() => this.getLinkSession(), 2000);
        }
      } catch (error) {
        console.error('Error getting link session:', error);
        setTimeout(() => this.getLinkSession(), 2000);
      }
    },
    
    startCheckingSession() {
      if (this.checkSessionInterval) {
        clearInterval(this.checkSessionInterval);
        this.checkSessionInterval = null;
      }
      
      // Instead of interval, use recursive setTimeout pattern
      this.checkSession();
    },
    
    async checkSession() {
      if (!this.uuid) return;
      
      try {
        const response = await this.$api.call('get_link_session', { uuid: this.uuid }, false);
        
        if (response.success && response.data) {
          // Update expires_at from response to recalculate remaining time
          if (response.data.expires_at) {
            this.expiresTime = response.data.expires_at;
            this.lastUpdated = Date.now();
          }
          
          // Check if we got a link token
          if (response.data.link_token) {
            // We have a link token, set it and redirect
            localStorage.setItem('lt', response.data.link_token);
            this.$store.commit('lt', response.data.link_token);
            
            // Fetch all necessary data before redirecting
            await Promise.all([
              this.$store.dispatch('getConfig'),
              this.$store.dispatch('getDevices'),
              this.$store.dispatch('getCustomerProfile')
            ]);
            
            // Now redirect to success page
            this.$router.push('/success?title=PC_LINK_OK');
            
            // No need to continue checking
            return;
          }
        } else if (response.errors && response.errors[0] && response.errors[0].code === 'ERROR_LINK_SESSION_EXPIRED') {
          // Session expired - CLEAN UP ALL INTERVALS before getting a new session
          this.cleanupIntervals();
          // Get a new session
          this.getLinkSession();
          return;
        }
      } catch (error) {
        console.error('Error checking link session:', error);
      }
      
      // Force update of timer display even if expires_at didn't change
      this.lastUpdated = Date.now();
      
      // Store timeout ID for cleanup
      this.checkSessionInterval = setTimeout(() => {
        this.checkSession();
      }, 1000);
    },
    
    async processLinkFromDevice() {
      try {
        if (!this.$store.getters.lt || !this.$route.query.uuid) {
          // Should not happen, redirect to home
          this.$router.push('/');
          return;
        }
        
        const response = await this.$api.call('get_link_session', {
          uuid: this.$route.query.uuid,
          set_link_token: this.$store.getters.lt
        }, false);
        
        if (response.success) {
          // Fetch all necessary data before redirecting
          await Promise.all([
            this.$store.dispatch('getConfig'),
            this.$store.dispatch('getDevices'),
            this.$store.dispatch('getCustomerProfile')
          ]);
          
          // Successfully linked, redirect to success page
          this.$router.push('/success?title=PC_LINK_AUTHORIZED');
        } else {
          // Error linking, redirect to home
          this.$router.push('/');
        }
      } catch (error) {
        console.error('Error processing link from device:', error);
        this.$router.push('/');
      }
    },
    
    // Helper method to clean up all intervals and timeouts
    cleanupIntervals() {
      if (this.checkSessionInterval) {
        clearTimeout(this.checkSessionInterval);
        this.checkSessionInterval = null;
      }
      
      if (this.pollingInterval) {
        clearTimeout(this.pollingInterval);
        this.pollingInterval = null;
      }
    }
  },
  created() {
    // Always reset the loading state when component is created
    this.isLoading = true;
    
    if (this.isDeviceLinked && this.hasUuid) {
      // Mode for linked devices with UUID parameter
      this.processLinkFromDevice();
    } else if (!this.isDeviceLinked) {
      // Mode for unlinked devices showing QR code
      this.getLinkSession();
    }
    // Third mode (isDeviceLinked && !hasUuid) just shows the message, no action needed
  },
  beforeDestroy() {
    // Clean up intervals/timeouts
    this.cleanupIntervals();
  },
  // Handle page navigation with keep-alive
  activated() {
    // If we're in the QR code mode and not linked, refresh the QR code
    if (!this.isDeviceLinked && !this.hasUuid) {
      this.getLinkSession();
    }
  },
  // Ensure cleanup when navigating away
  deactivated() {
    // Clean up all intervals and timeouts when navigating away
    this.cleanupIntervals();
  }
};
</script>

